@tailwind base;
@tailwind components;
@tailwind utilities;

.rtl{
    direction: rtl;
}

.ltr{
    direction: ltr;
}

.clicked{
    border-color: rgb(248, 166, 90);
    opacity: 1;
}

.background-svg::-webkit-slider-thumb{
    background: url("./slider-thumb.svg");
    background-size:contain;
    background-repeat: no-repeat;
    width: 9vw;
    height: 3vw;

}
.background-svg::-webkit-slider-thumb:active{
    background: url("./slider-thumb-active.svg");
    background-size: contain;
    background-repeat: no-repeat;
    width: 9vw;
    height: 3vw;
}

.title{
    font-size: clamp(0.5em , 1vw , 1.25em);
}

@media screen and (max-width: 680px){
    .title{
        font-size: clamp(0.5em , 2.5vw , 1.2em);
    }
    .background-svg::-webkit-slider-thumb{
        background: url("./slider-thumb.svg");
        background-size:contain;
        background-repeat: no-repeat;
        width: 10vw;
        height: 5vw;
    
    }
    .background-svg::-webkit-slider-thumb:active{
        background: url("./slider-thumb-active.svg");
        background-size: contain;
        background-repeat: no-repeat;
        width: 10vw;
        height: 5vw;
    }
}